/* general */
body {
  font-family: "Helvetica", Times, "Times New Roman", serif;
}
.miro-noscript {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 40%;
  font-weight: bold;
  font-size: 14pt;
}
.btn-default {
  border: 2px solid #ADADAD;
  background-color: #FFF;
  background-color: rgba(0, 0, 0, 0);
  transition: all 500ms ease;
}
.btn-custom {
  border-width: 1px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 1px 4px;
}
@media (prefers-color-scheme: dark) {
  .btn-custom {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 1px 4px;
  }
}
.readme-wrapper {
  max-height: 65vh;
  overflow: auto;
  position: relative;
}
.readme-wrapper table {
  margin: 10px 0;
}
.readme-wrapper td {
  padding: 10px;
}
.readme-wrapper tr:nth-child(even) {
  background-color: #f2f2f2;
}
@media (prefers-color-scheme: dark) {
  .readme-wrapper tr:nth-child(even) {
    background-color: #393e46;
  }
}
.readme-wrapper tr:nth-child(odd) {
  background-color: #fff;
}
@media (prefers-color-scheme: dark) {
  .readme-wrapper tr:nth-child(odd) {
    background-color: #4e545d;
  }
}
.readme-wrapper tr:hover {
  background-color: #ddd;
}
@media (prefers-color-scheme: dark) {
  .readme-wrapper tr:hover {
    background-color: #5f6771;
  }
}
.readme-wrapper th {
  position: -webkit-sticky;
  position: sticky;
  z-index: 500;
  padding-top: 15px;
  padding-bottom: 10px;
  background: #fff;
}
@media (prefers-color-scheme: dark) {
  .readme-wrapper th {
    background: #393e46;
  }
}
.readme-wrapper code {
  color: #3c8dbc;
  background-color: #ecf0f5;
}
@media (prefers-color-scheme: dark) {
  .readme-wrapper code {
    color: #00adb5;
    background-color: #292D33;
  }
}
.readme-preview-markdown {
  width: 100%;
  height: 65vh;
  resize: none;
  background-color: #efefef;
}
@media (prefers-color-scheme: dark) {
  .readme-preview-markdown {
    background-color: #292D33;
  }
}
@media (prefers-color-scheme: dark) {
  textarea.readme-preview-markdown:focus {
    outline-color: #00adb5;
  }
}
.readme-preview-output {
  height: 65vh;
  border: 1px solid #A9A9A9;
}
@media (prefers-color-scheme: dark) {
  .readme-preview-output {
    border: 1px solid #A9A9A9;
  }
}
.readme-preview-header {
  font-size: 22px;
  padding-bottom: 5px;
}
.btn-default:hover {
  color: #000;
}
.err-msg,
.config-array-err {
  margin: 5px;
  color: #F39619;
  font-weight: bold;
}
.input-validation-error {
  cursor: default;
  max-height: 110px;
  overflow: auto;
  padding-left: 20px;
}
.flex-design {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
.ttip {
  position: relative;
  text-decoration: underline;
  text-decoration: underline dotted #000;
}
.ttip span {
  visibility: hidden;
  position: absolute;
  z-index: 999;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  color: #ffffff;
  background: #000000;
  opacity: 0.9;
  bottom: 70%;
  left: 50%;
  width: 200px;
  margin-left: -100px;
  text-align: center;
}
.ttip:hover span {
  visibility: visible;
}
.ttip span:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -5px;
  border-top: 5px solid #000000;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
}
::selection {
  color: #fff;
  background: #3c8dbc;
}
@media (prefers-color-scheme: dark) {
  ::selection {
    background: #00adb5;
  }
}
::-moz-selection {
  color: #fff;
  background: #3c8dbc;
}
@media (prefers-color-scheme: dark) {
  ::-moz-selection {
    background: #00adb5;
  }
}
.box-header ::selection {
  background: #222d32;
}
@media (prefers-color-scheme: dark) {
  .box-header ::selection {
    background: #1d2121;
  }
}
.box-header ::-moz-selection {
  background: #222d32;
}
@media (prefers-color-scheme: dark) {
  .box-header ::-moz-selection {
    background: #1d2121;
  }
}
#shiny-tab-gamsinter .box-header ::selection {
  background: #222d32;
}
@media (prefers-color-scheme: dark) {
  #shiny-tab-gamsinter .box-header ::selection {
    background: #1d2121;
  }
}
#shiny-tab-gamsinter .box-header ::-moz-selection {
  background: #222d32;
}
@media (prefers-color-scheme: dark) {
  #shiny-tab-gamsinter .box-header ::-moz-selection {
    background: #1d2121;
  }
}
@media (prefers-color-scheme: dark) {
  #shiny-tab-gamsinter .box-body ::selection {
    background: #3c8dbc;
  }
}
@media (prefers-color-scheme: dark) {
  #shiny-tab-gamsinter .box-body ::-moz-selection {
    background: #3c8dbc;
  }
}
/* custom alerts */
.gmsalert {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  max-width: 400px;
  display: none;
  font-weight: 600;
  padding: 15px;
  z-index: 999;
  border: 1px solid #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 20px;
}
.gmsalert-success {
  color: #fff;
  background-color: #3c8dbc;
}
@media (prefers-color-scheme: dark) {
  .gmsalert-success {
    color: #fff;
    background-color: #00adb5;
  }
}
.gmsalert-error {
  background-color: #F39619;
}
.center-alert {
  position: fixed;
  top: 20%;
  left: 50%;
  right: unset;
  transform: translate(-50%, -50%);
}
.invalidInput {
  border: 2px solid #F39619;
}
/* title field in boxes */
.box-title {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.inline-el {
  display: inline;
}
.progress-bar {
  background-color: #f39619;
}
