}
.no-animations {
  -webkit-animation: none !important;
  -moz-animation: none !important;
  -o-animation: none !important;
  -ms-animation: none !important;
  animation: none !important;
}
.loading-input {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -16px;
  /* half of the spinner's height */
  margin-left: -16px;
  /* half of the spinner's width */
}
.loading-input-l {
  position: absolute;
  left: 25%;
  top: 50%;
}
.loading-input-r {
  position: absolute;
  left: 75%;
  top: 50%;
}
.renderer-wrapper {
  position: relative;
}
.renderer-wrapper .lds-ellipsis {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  margin-top: -33px;
  /* half of the spinner's height */
  margin-left: -33px;
  /* half of the spinner's width */
}
.renderer-wrapper .recalculating {
  z-index: -2;
}
.datepicker {
