/* material checkboxes */
.cb-label {
  display: inline;
  cursor: pointer;
}
.checkbox-material .checkbox {
  display: inline-block;
  line-height: 1;
}
.checkbox-material .checkbox input {
  opacity: 0;
}
.checkbox-material .checkbox input[disabled] + span {
  opacity: 0.5;
}
.checkbox-material .checkbox span {
  position: relative;
  display: inline-block;
  cursor: pointer;
  padding: 0.15em 1.5em 0.15em 0.15em;
  transition: all ease 0.2s;
  -webkit-transition: all ease 0.2s;
  -o-transition: all ease 0.2s;
  -moz-transition: all ease 0.2s;
  border-radius: 1em;
  background-color: grey;
  overflow: hidden;
}
@media (prefers-color-scheme: dark) {
  .checkbox-material .checkbox span {
    background-color: #848991;
  }
}
.checkbox-material .checkbox span::before {
  display: block;
  content: '';
  width: 1em;
  height: 1em;
  border-radius: 1em;
  background: white;
}
.checkbox-material .checkbox span::after {
  position: absolute;
  display: block;
  text-transform: uppercase;
  font-size: 0.5em;
  font-style: normal;
  top: 0;
  color: #fff;
  line-height: 1;
  left: 0;
  padding-left: 2em;
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  height: 1em;
  bottom: 0;
  margin: auto;
}
.checkbox-material .checkbox :checked + span::after {
  padding-left: 0;
  padding-right: 2em;
}
.checkbox-material .checkbox :checked + span {
  padding-right: 0.15em;
  padding-left: 1.5em;
  background-color: #f39619;
}
@media (prefers-color-scheme: dark) {
  .checkbox-material .checkbox :checked + span {
    background-color: #00adb5;
  }
}
.checkbox-material .shiny-input-container {
  display: inline-block;
  zoom: 1;
  *display: inline;
  width: 75px;
  margin: 0;
}
.checkbox-material {
  cursor: pointer;
  align-items: center;
}
