/* Hypercube module */
.centered-div {
  text-align: center;
  font-size: 20px;
  margin-top: 25%;
}
.centered-div button {
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.grid-container {
  grid-template-columns: 325px 150px 325px 75px;
  grid-gap: 5px;
  padding: 5px;
}
.grid-container > div {
  text-align: center;
  font-size: 16px;
}
.grid-SelectorsWrapper {
  grid-column-start: 1;
  grid-column-end: 4;
}
.grid-ButtonsWrapper {
  display: grid;
  grid-column-start: 1;
  grid-column-end: 4;
  padding-top: 15px;
  padding-bottom: 15px;
}
.item-line {
  grid-template-columns: 325px 150px 325px 75px;
  grid-template-rows: 40px;
  grid-gap: 5px;
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
}
.item-name {
  grid-column-start: 1;
  grid-column-end: 2;
  align-self: start;
  text-align: left;
}
.item-scen-drop {
  grid-column-start: 2;
  grid-column-end: 3;
  align-self: start;
}
.item-search-crit {
  grid-column-start: 3;
  grid-column-end: 4;
  align-self: start;
}
.item-delete {
  text-align: right;
}
.btn-item-delete {
  background-color: #fff;
}
@media (prefers-color-scheme: dark) {
  .btn-item-delete {
    background-color: #393e46;
  }
}
.item-add-block {
  display: grid;
  grid-template-columns: 75px 250px 605px;
  grid-template-rows: 50px;
  align-self: center;
  padding-left: 5px;
}
.item-and {
  grid-column-start: 1;
  grid-column-end: 2;
}
.item-dropdown {
  grid-column-start: 2;
  grid-column-end: 3;
}
.item-or-query {
  grid-column-start: 1;
  grid-column-end: 3;
  margin-top: 15px;
  margin-bottom: 25px;
}
.and-wrapper {
